import {Routes} from '@angular/router';

import {MaterielAddComponent}  from './materiel.add/materiel.add.component';
import {MaterielListComponent} from "./materiel.list/materiel.list.component";
import {MaterielEditComponent} from "./materiel.edit/materiel.edit.component";

export const MaterielRoutes:Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [{
    path: 'add', component: MaterielAddComponent
  },
    {path: 'list', component: MaterielListComponent},
    {path: 'edit/:id', component: MaterielEditComponent},
  ]
}];



