"use strict";

import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Materiel} from "../metier/materiel.metier";
import {FormBuilder, FormGroup} from "@angular/forms";
import {BaseComponent} from "../../../core/component/base-component";
import {MaterielService} from "../service/materiel.service";
import {PageTitleService} from "../../../core/page-title/page-title.service";

@Component({
  selector: 'ms-materiel-list',
  templateUrl: './materiel.list.component.html',
  styleUrls: ['./materiel.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class MaterielListComponent extends BaseComponent implements OnInit {

  public materielForm: FormGroup;
  private materiel: Materiel;
  private materielList: Array<Materiel> = [];

  constructor(private fb: FormBuilder,
              private materielService: MaterielService,
              private pageTitleService: PageTitleService,) {
    super();
    this.createForm();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES MATERIELS");
    this.loadMaterielList();
  }

  createForm() {
    this.materielForm = this.fb.group({
      libelle: '',
      prix: null,
    });
  }


  findMaterielList(materielForm: any) {
    this.materiel = new Materiel(materielForm);
    this.materielService.findMaterielListByCriteria(this.materiel)
      .subscribe(materielList => {
        this.materielList = materielList;
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  private loadMaterielList() {
    this.materielService.getMaterielList()
      .subscribe(materielList => {
          this.materielList = materielList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }


}






