import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {MaterielAddComponent} from './materiel.add/materiel.add.component';
import {MaterielRoutes} from './materiel.routing';
import {MaterielService} from "./service/materiel.service";
import {MaterielListComponent} from "./materiel.list/materiel.list.component";
import {MaterielEditComponent} from "./materiel.edit/materiel.edit.component";
import {DataTableModule} from "angular2-datatable";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(MaterielRoutes)
  ],
  declarations: [
    MaterielAddComponent,
    MaterielListComponent,
    MaterielEditComponent,
  ],
  providers: [
    MaterielService,
  ],
})

export class MaterielModule {
}



