import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';
import { Materiel } from "../metier/materiel.metier";
import { MaterielService } from "../service/materiel.service";
import { BaseComponent } from "../../../core/component/base-component";
import { fadeInAnimation } from "../../../core/route-animation/route.animation";
import { PageTitleService } from "../../../core/page-title/page-title.service";

const password = new FormControl('', Validators.required);
const confirmPassword = new FormControl('', CustomValidators.equalTo(password));

@Component({
  selector: 'ms-materiel-add',
  templateUrl: './materiel.add.component.html',
  styleUrls: ['./materiel.add.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class MaterielAddComponent extends BaseComponent implements OnInit {

  public materielForm: FormGroup;
  private materiel: Materiel;

  constructor(private fb: FormBuilder, private pageTitleService: PageTitleService, private materielService: MaterielService) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("AJOUTER UN MATERIEL");
    this.materielForm = this.fb.group({
      libelle: ['', Validators.required],
      prix: [null, Validators.required],
      description: '',
      prixVarie: false,
    });
  }

  saveMateriel(materielForm: any) {
    this.materiel = new Materiel(materielForm);
    this.materielService.saveMateriel(this.materiel)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.reset();
        this.router.navigate(["/admin/materiel/list"]);
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  reset() {
    this.materielForm.reset();
  }


}






