import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PageTitleService } from '../../../core/page-title/page-title.service';
import { fadeInAnimation } from "../../../core/route-animation/route.animation";
import { Materiel } from "../metier/materiel.metier";
import { MaterielService } from "../service/materiel.service";
import { BaseComponent } from "../../../core/component/base-component";
import { ActivatedRoute, Params } from "@angular/router";

@Component({
  selector: 'ms-materiel-edit',
  templateUrl: './materiel.edit.component.html',
  styleUrls: ['./materiel.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class MaterielEditComponent extends BaseComponent implements OnInit {
  private materiel: Materiel;
  public materielForm: FormGroup;


  constructor(private fb: FormBuilder,
    private route: ActivatedRoute,
    private pageTitleService: PageTitleService,
    private materielService: MaterielService) {
    super();
  }

  ngOnInit() {
    this.loadMateriel();
    this.pageTitleService.setTitle("MODIFIER UN MATERIEL");
    this.materielForm = this.fb.group({
      id: [Validators.required],
      libelle: ['', Validators.required],
      prix: [null, Validators.required],
      description: '',
      prixVarie: false,
    });
  }


  updateMateriel(materielForm: any) {
    this.materiel = new Materiel(materielForm);
    this.materielService.saveMateriel(this.materiel)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/admin/materiel/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  loadMateriel() {
    this.route.params.switchMap((params: Params) => this.materielService.getMateriel(+params['id']))
      .subscribe(materiel => {
        this.materiel = materiel;
        this.materielForm.patchValue(this.materiel);
      }, error => this.showError(error.status, error.json().message)
      );
  }

  deleteMateriel(materielForm: any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }
    this.materiel = new Materiel(materielForm);
    this.materielService.deleteMateriel(this.materiel)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/admin/materiel/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
      ;
  }


}






